<template>
    <div class="withdraw">
        <div class="row d-flex">
            <div class="col-md-6">
                <div class="card">
                    <div class="card-body">
                        <h4 class="fw-bold py-3 mb-4 d-flex">
                            <span class="mr-auto">
                              {{ $trans('Thông tin tài khoản') }}
                            </span>
                        </h4>
                        <slot v-if="accounts.bank">
                            <div class="table-responsive">
                                <table class="table table-striped">
                                    <thead>
                                    <tr>
                                        <th>{{ $trans('Thông tin tài khoản') }}</th>
                                        <th></th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    <tr>
                                        <td><span class="d-block" v-if="payout_banks"><strong>[{{ accounts.bank}}] - </strong> {{ bank }}</span>
                                            {{ accounts.account_name_bank }} - {{ accounts.account_number }}</td>
                                        <td></td>
                                    </tr>
                                    <tr>
                                        <td><img :src="cccd_up_img"></td>
                                        <td><img :src="cccd_down_img"></td>
                                    </tr>
                                    </tbody>
                                </table>
                            </div>
                        </slot>
                        <slot v-else>
                            <div class="alert alert-warning" >
                                {{ $trans('Bạn chưa có tài khoản ngân hàng') }}
                            </div>
                        </slot>
                    </div>
                </div>
            </div>
            <div class="col-md-6" v-if="!accounts.bank">
                <div class="card">
                    <div class="card-body">
                        <h5 class="mb-3">
                            {{ $trans('Thêm tài khoản ngân hàng') }}
                            <button @click="addForm = false" class="close">
                                <svg viewBox="0 0 24 24" width="24" height="24" stroke="currentColor" stroke-width="2"
                                     fill="none" stroke-linecap="round" stroke-linejoin="round" class="css-i6dzq1">
                                    <line x1="18" y1="6" x2="6" y2="18"></line>
                                    <line x1="6" y1="6" x2="18" y2="18"></line>
                                </svg>
                            </button>
                        </h5>
                        <small>{{ $trans('Các trường có dấu (*) là bắt buộc.') }}</small>
                        <hr>
                        <div v-if="alert" class="alert" v-bind:class="'alert-'+alert.type">
                            {{ alert.message }}
                        </div>
                        <div class="form-group">
                            <label>{{ $trans('Tên ngân hàng') }} (*)</label>
                            <Select2 v-model="addFields.bank" :options="payout_banks"/>
                            <slot v-if="errors && errors['bank']">
                                <div class="text-danger" v-for="error in errors['bank']"
                                     v-bind:key="error">
                                    <small>{{ error }}</small>
                                </div>
                            </slot>
                        </div>
                        <div class="form-group">
                            <label>{{ $trans('Chủ tài khoản') }} (*)</label>
                            <input v-model="addFields.account_name_bank" class="form-control" type="text">
                            <slot v-if="errors && errors['account_name_bank']">
                                <div class="text-danger" v-for="error in errors['account_name_bank']"
                                     v-bind:key="error">
                                    <small>{{ error }}</small>
                                </div>
                            </slot>
                        </div>
                        <div class="form-group">
                            <label>{{ $trans('Số tài khoản') }} (*)</label>
                            <input v-model="addFields.account_number" class="form-control" type="text">
                            <slot v-if="errors && errors['account_number']">
                                <div class="text-danger" v-for="error in errors['account_number']"
                                     v-bind:key="error">
                                    <small>{{ error }}</small>
                                </div>
                            </slot>
                        </div>
                        <div class="form-group">
                            <label>{{ $trans('CCCD/CMND mặt trước') }} (*)</label>
                            <input class="form-control" v-on:change="onFileChange($event,'up')" type="file"
                                   accept="image/*">
                            <slot v-if="errors && errors['cccd_up']">
                                <div class="text-danger" v-for="error in errors['cccd_up']"
                                     v-bind:key="error">
                                    <small>{{ error }}</small>
                                </div>
                            </slot>
                        </div>
                        <div class="form-group" >
                            <label>{{ $trans('CCCD/CMND mặt sau') }} (*)</label>
                            <input class="form-control" v-on:change="onFileChange($event,'down')" type="file"
                                   accept="image/*">
                            <slot v-if="errors && errors['cccd_down']">
                                <div class="text-danger" v-for="error in errors['cccd_down']"
                                     v-bind:key="error">
                                    <small>{{ error }}</small>
                                </div>
                            </slot>
                        </div>
                        <button @click="store" class="btn btn-primary m-t-20">
                            <div v-if="process" class="spinner-border spinner-border-sm text-secondary"></div>
                            {{ $trans('Lưu') }}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";
import banks from './mixins/banks.json'

export default {
    name: "WithdrawAccount",
    data: function () {
        return {
            addForm: false,
            filterQuery: {
                page: 1
            },
            addFields: {
                bank: '',
                account_name_bank: '',
                account_number: '',
                cccd_up: '',
                cccd_down: '',
            },
            alert: null,
            errors: {},
            process: false,
            accounts: {
                bank:''
            },
            cccd_up_img: '',
            cccd_down_img: '',
            updateFields: {}
        }
    },
    methods: {
        paginate: function (pageNum) {
            this.filterQuery.page = pageNum;
            this.index();
        },
        index: function () {
            axios({
                url: this.$root.$data.api_url + '/api/player',
                params: this.filterQuery,
                method: 'GET'
            })
                .then(resp => {
                    this.accounts = resp.data.results;
                    this.cccd_up_img = this.$root.$data.api_url + '/storage/' + this.accounts.cccd_up;
                    this.cccd_down_img = this.$root.$data.api_url + '/storage/' + this.accounts.cccd_down;
                })
                .catch(err => {
                    console.log(err)
                })
        },

        onFileChange(e, type) {
            var files = e.target.files || e.dataTransfer.files;
            if (!files.length)
                return;
            this.createImage(files[0], type);
        },
        createImage(file, type) {
            // var reader = new FileReader();
            var vm = this;
            if (type === 'up') {
                vm.addFields.cccd_up = file;
            } else {
                vm.addFields.cccd_down = file;
            }

        },

        store: function () {
            if (this.process == false) {
                this.process = true;
                this.errors = {}
                this.alert = null
                const formData = new FormData();
                formData.append("bank", this.addFields.bank);
                formData.append("account_name_bank", this.addFields.account_name_bank);
                formData.append("account_number", this.addFields.account_number);
                formData.append("cccd_up", this.addFields.cccd_up);
                formData.append("cccd_down", this.addFields.cccd_down);

                axios({
                    url: this.$root.$data.api_url + '/api/player/add-bank', data: formData, method: 'POST', header: {
                        'Content-Type': 'multipart/form-data;',
                    }
                })
                    .then(resp => {
                        if (resp.data.error_code === 0) {
                            this.alert = {
                                type: 'success',
                                message: 'Bạn đã lưu thành công.'
                            }
                            this.index();
                        } else {
                            this.alert = {
                                type: 'warning',
                                message: resp.data.message
                            }
                            this.errors = resp.data.errors || {};
                        }
                        this.process = false;
                    })
                    .catch(err => {
                        console.log(err)
                        this.process = false;
                    })
            }
        },
    },
    created() {
        this.index()
    },
    computed:{
        payout_banks :function (){
            return banks;
        },
        bank :function (){
            if(!this.payout_banks || !this.accounts){
                return '';
            }
            let bank_pay = this.payout_banks[this.accounts.bank - 1]
            if(bank_pay){
                return bank_pay[Object.keys(bank_pay)[1]]
            }
            return '';
        }
    }

}
</script>

<style scoped>

</style>
